<template>
  <div class="main-content">
    <!-- 列表页 -->
    <div v-if="showFlag">
      <el-form :inline="true" :model="searchForm" class="form-content">
                                            <el-form-item label="汽车名称">
                <el-input v-model="searchForm.qichemingcheng" 
                    placeholder="汽车名称" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="汽车类型">
                <el-input v-model="searchForm.qicheleixing" 
                    placeholder="汽车类型" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="颜色">
                <el-input v-model="searchForm.yanse" 
                    placeholder="颜色" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="座位">
                <el-input v-model="searchForm.zuowei" 
                    placeholder="座位" clearable></el-input>
              </el-form-item>
                                                                                                                                                  <el-form-item>
          <el-button round @click="search()">查询</el-button>
          <el-button
            v-if="isAuth('sirendingzhi','新增')"
            type="primary"
            @click="addOrUpdateHandler()"
            round
          >新增</el-button>
          <el-button
            v-if="isAuth('sirendingzhi','删除')"
            :disabled="dataListSelections.length <= 0"
            type="danger"
            @click="deleteHandler()"
            round
          >删除</el-button>
                  </el-form-item>
      </el-form>
      <div class="table-content">
        <el-table
            v-if="isAuth('sirendingzhi','查看')"
            :data="dataList"
            border
            v-loading="dataListLoading"
            @selection-change="selectionChangeHandler"
            style="width: 100%;">
            <el-table-column
                type="selection"
                header-align="center"
                align="center"
                width="50">
            </el-table-column>
                                            <el-table-column
                    prop="qichemingcheng"
                    header-align="center"
                    align="center"
                    sortable
                    label="汽车名称">
                    <template slot-scope="scope">
                      {{scope.row.qichemingcheng}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="qicheleixing"
                    header-align="center"
                    align="center"
                    sortable
                    label="汽车类型">
                    <template slot-scope="scope">
                      {{scope.row.qicheleixing}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="yanse"
                    header-align="center"
                    align="center"
                    sortable
                    label="颜色">
                    <template slot-scope="scope">
                      {{scope.row.yanse}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="zuowei"
                    header-align="center"
                    align="center"
                    sortable
                    label="座位">
                    <template slot-scope="scope">
                      {{scope.row.zuowei}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="pailiang"
                    header-align="center"
                    align="center"
                    sortable
                    label="排量">
                    <template slot-scope="scope">
                      {{scope.row.pailiang}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="beizhu"
                    header-align="center"
                    align="center"
                    sortable
                    label="备注">
                    <template slot-scope="scope">
                      {{scope.row.beizhu}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="riqi"
                    header-align="center"
                    align="center"
                    sortable
                    label="日期">
                    <template slot-scope="scope">
                      {{scope.row.riqi}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="yonghuming"
                    header-align="center"
                    align="center"
                    sortable
                    label="用户名">
                    <template slot-scope="scope">
                      {{scope.row.yonghuming}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="xingming"
                    header-align="center"
                    align="center"
                    sortable
                    label="姓名">
                    <template slot-scope="scope">
                      {{scope.row.xingming}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shouji"
                    header-align="center"
                    align="center"
                    sortable
                    label="手机">
                    <template slot-scope="scope">
                      {{scope.row.shouji}}
                    </template>
                </el-table-column>
                                                    <el-table-column
                header-align="center"
                align="center"
                label="操作">
                <template slot-scope="scope">
                                <el-button v-if="isAuth('sirendingzhi','查看')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id,'info')">详情</el-button>
                                                                <el-button v-if="isAuth('sirendingzhi','定车')" type="text" icon="el-icon-edit" size="small" @click="dingzhixinxiCrossAddOrUpdateHandler(scope.row,'cross')">定车</el-button>
                                                                <el-button v-if="isAuth('sirendingzhi','修改')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id)">修改</el-button>
                <el-button v-if="isAuth('sirendingzhi','删除')" type="text" icon="el-icon-delete" size="small" @click="deleteHandler(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
                                                                                                                                                                                <el-pagination
          @size-change="sizeChangeHandle"
          @current-change="currentChangeHandle"
          :current-page="pageIndex"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          :total="totalPage"
          layout="total, sizes, prev, pager, next, jumper"
          class="pagination-content"
        ></el-pagination>
      </div>
    </div>
    <!-- 添加/修改页面  将父组件的search方法传递给子组件-->
    <add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

            <dingzhixinxi-cross-add-or-update v-if="dingzhixinxiCrossAddOrUpdateFlag" :parent="this" ref="dingzhixinxiCrossaddOrUpdate"></dingzhixinxi-cross-add-or-update>
        
    
      </div>
</template>
<script>
import AddOrUpdate from "./add-or-update";
import dingzhixinxiCrossAddOrUpdate from "../dingzhixinxi/add-or-update";
export default {
  data() {
    return {
                                                                                                                                                                                                                                                searchForm: {
        key: ""
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      addOrUpdateFlag:false,
                  dingzhixinxiCrossAddOrUpdateFlag: false,
                };
  },
  mounted() {
    this.init();
    this.getDataList();
  },
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
    }
  },
  components: {
    AddOrUpdate,
            dingzhixinxiCrossAddOrUpdate,
          },
  methods: {
            dingzhixinxiCrossAddOrUpdateHandler(row,type){
      this.showFlag = false;
      this.addOrUpdateFlag = false;
      this.dingzhixinxiCrossAddOrUpdateFlag = true;
      this.$storage.set('crossObj',row);
      this.$storage.set('crossTable','sirendingzhi');
      this.$nextTick(() => {
        this.$refs.dingzhixinxiCrossaddOrUpdate.init(row.id,type);
      });
    },
                    init () {
                                                                                                                                                                                                                                                                                                                                            },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
      }
                                          if(this.searchForm.qichemingcheng!='' && this.searchForm.qichemingcheng!=undefined){
            params['qichemingcheng'] = '%' + this.searchForm.qichemingcheng + '%'
          }
                                                            if(this.searchForm.qicheleixing!='' && this.searchForm.qicheleixing!=undefined){
            params['qicheleixing'] = '%' + this.searchForm.qicheleixing + '%'
          }
                                                            if(this.searchForm.yanse!='' && this.searchForm.yanse!=undefined){
            params['yanse'] = '%' + this.searchForm.yanse + '%'
          }
                                                            if(this.searchForm.zuowei!='' && this.searchForm.zuowei!=undefined){
            params['zuowei'] = '%' + this.searchForm.zuowei + '%'
          }
                                                                                                                                                                  this.$http({
        url: "sirendingzhi/page",
        method: "get",
        params: params
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
        // 下载
    download(file){
      window.open(`${file}`)
    },
    // 删除
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "sirendingzhi/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
